<template>
  <div class="detail">
    <detail-nav ref="detailNav" @navTitleClick="titleClick"></detail-nav>
    <scroll-view ref="scroll" @scroll="scroll" :probe-type="probeType" :listen-scroll="listenScroll"
                 class="scroll-wrapper" :data="data">
      <detail-swiper :banners="banners" class="swiper" @swiperImgLoad="goodsImageLoad"/>
      <goods-info :goods="goods"></goods-info>
      <shop-info :shop="shop"></shop-info>
      <goods-desc :detail-info="detailInfo" @detailImgLoad="goodsImageLoad"></goods-desc>
      <goods-params ref="params" :goods-params="goodsParams"></goods-params>
      <detail-comment ref="comment" :commentInfo="commentInfo" @recommendImgLoad="goodsImageLoad"></detail-comment>
      <goods-list ref="recommend" :goodsList="recommend"/>
    </scroll-view>
    <back-top @click.native="backTop" v-show="isShow"></back-top>
    <detail-bottom-bar @addToCar="addToCar"></detail-bottom-bar>
  </div>
</template>

<script>
import {getGoodsDetail, getRecommendData, Goods, GoodsParamsInfo, Shop,} from "../../network/detail";
import {backTop} from "@/common/mixin.js";
import {debounce} from "@/common/utils";
import {mapActions} from "vuex";

import DetailNav from "./DetailNav.vue";
import ScrollView from "../../components/scroll/ScrollView.vue";
import DetailSwiper from "../../components/swiper/DetailSwiper.vue";
import ShopInfo from "./ShopInfo.vue";
import GoodsInfo from "./GoodsInfo.vue";
import GoodsDesc from "./GoodsDesc.vue";
import GoodsParams from "./GoodsParams.vue";
import DetailComment from "./DetailComment.vue";
import GoodsList from "../../components/goodslist/GoodsList.vue";
import DetailBottomBar from "@/views/details/DetailBottomBar";

export default {
  name: "Detail",
  components: {
    DetailBottomBar,
    DetailNav,
    ScrollView,
    DetailSwiper,
    ShopInfo,
    GoodsInfo,
    GoodsDesc,
    GoodsParams,
    DetailComment,
    GoodsList,
  },
  data() {
    return {
      id: "",
      data: [],
      banners: [],
      goods: {},
      shop: {},
      detailInfo: {},
      goodsParams: {},
      commentInfo: [],
      recommend: [],
      titleScrollY: [],
      getOffsetY: null,
      probeType: 3,
      listenScroll: true,
      curIdx: 0
    };
  },
  computed: {},
  methods: {
    ...mapActions(["addCar"]),
    addToCar() {
      const products = {}
      products.image = this.banners[0]
      products.title = this.goods.title
      products.desc = this.goods.desc
      products.price = this.goods.newPrice
      products.lowPrice = this.goods.lowPrice
      products.id = this.id
      // this.$store.dispatch("addCar", products).then(result => {
      //   console.log(result)
      //   //  显示添加到购物车成功弹框
      //
      // })
      this.addCar(products).then(result => {
        console.log(result)
        //  显示添加到购物车成功弹框
        this.$toast.showToast("添加到购物车成功",2000)
      })

    },
    titleClick(index) {
      this.$refs.scroll.scrollTo(0, -this.titleScrollY[index], 200)
    },
    goodsImageLoad() {
      this.getOffsetY();
    },
    scroll(pos) {
      this.listenBackScroll(pos);
      let posY = -pos.y
      for (let i = 0; i < this.titleScrollY.length - 1; i++) {
        if (this.curIdx !== i && (posY >= this.titleScrollY[i] && posY < this.titleScrollY[i + 1])) {
          this.curIdx = i
          this.$refs.detailNav.curIndex = this.curIdx
        }
      }
    }
  },
  mixins: [backTop],
  created() {
    this.id = this.$route.params.id;
    getGoodsDetail(this.id).then((response) => {
      this.banners = response.result.itemInfo.topImages;
      this.goods = new Goods(
          response.result.itemInfo,
          response.result.columns,
          response.result.shopInfo.services
      );
      this.shop = new Shop(response.result.shopInfo);
      this.detailInfo = response.result.detailInfo;
      this.goodsParams = new GoodsParamsInfo(
          response.result.itemParams.info,
          response.result.itemParams.rule
      );

      if (response.result.rate.cRate !== 0) {
        this.commentInfo = response.result.rate.list;
      }
    });

    getRecommendData().then((response) => {
      this.recommend = response.data.list;
    });

    this.getOffsetY = debounce(() => {
      this.titleScrollY = []
      if (this.$refs.params && this.$refs.params.$el) {
        this.titleScrollY.push(0)
        this.titleScrollY.push(this.$refs.params.$el.offsetTop)
        this.titleScrollY.push(this.$refs.comment.$el.offsetTop)
        this.titleScrollY.push(this.$refs.recommend.$el.offsetTop)
        this.titleScrollY.push(Number.MAX_VALUE)
      }
      console.log(this.titleScrollY)
    }, 1000)
  },
  mounted() {
  },
};
</script>

<style lang="less" scoped>
.detail {
  height: 100vh;
  position: relative;
  z-index: 20;
  background-color: #fff;

  .scroll-wrapper {
    overflow: hidden;
    position: absolute;
    top: 44px;
    left: 0;
    right: 0;
    bottom: 50px;
  }
}
</style>
